@font-face {
  font-family: "Digital7";
  src: url(/react-clock/static/media/Digital7Mono-B1g5.58045dab.ttf) format("truetype");
  font-display: swap;
}

:root,
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: antiquewhite;
  color: blue;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.album-item {
  border: 2px solid blue;
  border-radius: 20%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 20px;
}

.album-item h2 {
  margin-left: auto;
  margin-right: auto;
  max-width: 90%;
  text-align: center;
}

.album-item div {
  margin-left: auto;
  margin-right: auto;
  max-width: 95%;
  text-align: center;
  height: 175px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.album-item p {
  font-family: "Digital7";
  font-size: 20px;
  margin-bottom: 0;
  padding-bottom: 0;
  padding-top: 0;
}

.album-item img {
  margin-right: 5px;
  margin-inline: 5px;
  max-width: 35px;
}

body {
  padding-block: 20px;
  padding-inline: 10px;
}
h1,
h2,
p {
  text-align: center;
}
.sk-album {
  display: grid;
  grid-template-columns: 1fr;
  -webkit-margin-start: 10px;
          margin-inline-start: 10px;
  -webkit-margin-end: 10px;
          margin-inline-end: 10px;
  grid-gap: 10px;
}
@media screen and (min-width: 576px) {
  .sk-album {
    grid-template-columns: 1fr 1fr;
  }
}
@media screen and (min-width: 992px) {
  .sk-album {
    grid-template-columns: repeat(3, 1fr);
  }
}
input {
  padding: 10px 20px;
  border-color: blue;
  border-style: solid;
  border-width: 1px;
  background-color: white;
  width: 320px;
}

